/* Dark enterprise theme */
.App {
  min-height: 100vh;
  background-color: #1E1E2F;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  line-height: 1.6;
}

/* Header */
.header {
  padding: 2rem 1rem;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  letter-spacing: -0.02em;
}

/* Main content */
.main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Hero text */
.hero-text {
  padding: 3rem 1rem;
  text-align: center;
}

.hero-text p {
  font-size: 1.25rem;
  line-height: 1.7;
  color: #e0e0e0;
  max-width: 800px;
  margin: 0 auto;
  font-weight: 400;
}

/* Features section */
.features {
  padding: 2rem 0 4rem;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 0 1rem;
}

.feature-card {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  text-align: center;
}

.feature-card:hover {
  transform: translateY(-4px);
  background-color: rgba(255, 255, 255, 0.08);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.feature-icon {
  font-size: 3rem;
  color: #4FC3F7;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  background-color: rgba(79, 195, 247, 0.1);
  border-radius: 12px;
  margin: 0 auto 1.5rem;
}

.feature-icon .fas {
  font-size: 2.5rem;
  color: #4FC3F7;
}

.feature-title {
  font-size: 1.375rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 1rem;
  letter-spacing: -0.01em;
}

.feature-description {
  font-size: 1rem;
  color: #cccccc;
  line-height: 1.6;
  margin: 0;
}

/* Footer */
.footer {
  padding: 2rem 1rem;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.3);
  color: #999999;
  font-size: 0.875rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .header {
    padding: 1.5rem 1rem;
  }

  .header h1 {
    font-size: 2rem;
  }

  .hero-text {
    padding: 2rem 1rem;
  }

  .hero-text p {
    font-size: 1.125rem;
  }

  .features-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .feature-card {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .header h1 {
    font-size: 1.75rem;
  }

  .hero-text p {
    font-size: 1rem;
  }

  .feature-card {
    padding: 1.25rem;
  }
}

/* Images section */
.images-section {
  padding: 2rem 0 4rem;
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  padding: 0 1rem;
}

.image-container {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  overflow: hidden;
}

.image-container:hover {
  transform: scale(1.02);
  background-color: rgba(255, 255, 255, 0.08);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.image-container img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  display: block;
  transition: all 0.3s ease;
}

.image-container:hover img {
  transform: scale(1.05);
}

/* Responsive for images */
@media (max-width: 768px) {
  .images-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .image-container {
    padding: 0.75rem;
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.feature-card,
.image-container {
  animation: fadeInUp 0.6s ease-out forwards;
}

.feature-card:nth-child(2),
.image-container:nth-child(2) {
  animation-delay: 0.1s;
}

.feature-card:nth-child(3),
.image-container:nth-child(3) {
  animation-delay: 0.2s;
}

.feature-card:nth-child(4),
.image-container:nth-child(4) {
  animation-delay: 0.3s;
}